import { createSlice } from '@reduxjs/toolkit';

export const FieldDefinitionsSlice = createSlice({
  name: 'FieldDefinitions',
  initialState: {
    contacts: {
      placeholder: "",
      options: {}
    },
  },
  reducers: {
    setContacts: (state, action) => {
      state.contacts = action.contacts;
    },
  },
});

// Action creators are generated for each case reducer function
export const { setContacts } = FieldDefinitionsSlice.actions;

export default FieldDefinitionsSlice.reducer;
